.navbar {
    background: rgb(44, 44, 44);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 2000px;
  }
  
  .navbar-logo {
    color: #ffc800;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    font-weight: 500;
    width: 34vw;
  }
  
  .fa-firstdraft {
    margin-right: 0.5rem;
    font-size: 1.8rem;
    margin-top: 0.5rem;
  }
  
  .nav-menu {
    display: grid;
    display: flex;
    flex-direction: row;
    list-style: none;
    text-align: center;
    width: 45vw;
    justify-content: flex-end;
    margin-right: 2rem;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .nav-links:hover {
    border-bottom: 4px solid #ffc800;
    transition: all 0.2s ease-out;
    color: #ffc800;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }



  @media screen and (max-width: 960px) {
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
      border-top: 1px solid #ffc800;
    }
  
    .active {
      background: rgb(44, 44, 44);
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #ffc800;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      font-size: 1.6rem;
      position: absolute;
      width: auto;
      top: 0;
      left: 0;
      transform: translate(4%, 60%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }

    .nav-links-mobile {
        display: block;
        text-align: center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: #fff;
        padding: 14px 20px;
        border: 1px solid #fff;
        transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #ffc800;
      color: rgb(44, 44, 44);
      transition: 250ms;
    }
  }

  @media screen and (max-width: 430px) {
    .navbar-logo {
        font-size: 1.4rem;
    }

    .fa-times, .fa-bars {
        font-size: 1.4rem;
    }
  }